"use client";

import React, { useState, useEffect } from "react";
import Button from "./Button";

const steps = [
  "Analyzing the webpage content...",
  "Identifying key elements to interact with...",
  "Filling out forms and clicking buttons...",
  "Navigating to the next page...",
  "Completing the task successfully!",
];

const AIAgentDemo: React.FC = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [running, setRunning] = useState(false);

  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (running && currentStep < steps.length - 1) {
      timer = setTimeout(() => {
        setCurrentStep((prev) => prev + 1);
      }, 2000);
    }
    return () => clearTimeout(timer);
  }, [currentStep, running]);

  const startDemo = () => {
    setCurrentStep(0);
    setRunning(true);
  };

  const resetDemo = () => {
    setCurrentStep(0);
    setRunning(false);
  };

  return (
    <div className="max-w-xl rounded-lg bg-gray-900 p-6 shadow-lg text-white">
      <h2 className="mb-4 text-2xl font-semibold">AI Agent Interaction Demo</h2>
      <p className="mb-6">
        This demo simulates how the Project Mariner AI agent interacts with webpages to complete tasks.
      </p>
      <div className="mb-6 min-h-[80px] rounded border border-gray-700 bg-gray-800 p-4">
        <p>{steps[currentStep]}</p>
      </div>
      {!running ? (
        <Button onClick={startDemo}>Start AI Interaction</Button>
      ) : (
        <Button variant="secondary" onClick={resetDemo}>
          Reset
        </Button>
      )}
    </div>
  );
};

export default AIAgentDemo;
